nav ul {
    list-style-type: none;
    padding: 0;
  }
  
  nav ul li {
    display: inline;
    margin-right: 20px;
  }
  
  nav ul li a {
    text-decoration: none;
    color: #007bff;
  }
  
  nav ul li a:hover {
    text-decoration: underline;
  }

  .navBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #007bff; /* Example background color */
    color: white;
  }
  
  .logo {
    /* Remove the flex: 1; if it was previously set here */
    height: 60px;
  }
  
  .navLinks {
    display: flex;
    justify-content: flex-end; /* Keeps nav links to the right */
    list-style-type: none; /* Removes default list styling */
    margin-left: auto; /* Pushes the navLinks to the far right */
  }
  
  .navLinks a {
    color: white;
    text-decoration: none;
    padding: 0 10px; /* Adjust based on your design */
  }
  
  .navLinks a:hover {
    text-decoration: underline;
  }
  
  